// pages/api/prescriptions/index.ts

import type { NextApiRequest, NextApiResponse } from "next";
import { prisma } from "../../../lib/prisma";
import { requireRole } from "../../../server/rbac";

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  try {
    // POST - Créer une ordonnance
    if (req.method === "POST") {
      const session = await requireRole(req, res, ["ADMIN", "DOCTOR", "RECEPTIONIST"]);
      if (!session) return;

      const { consultationId, medications, instructions } = req.body;

      if (!consultationId || !Array.isArray(medications) || !instructions) {
        return res.status(400).json({ error: "Données incomplètes" });
      }

      // Vérifier que la consultation existe
      const consultation = await prisma.consultation.findUnique({
        where: { id: parseInt(consultationId) },
      });
      if (!consultation) return res.status(404).json({ error: "Consultation introuvable" });

      // Créer l'ordonnance
      const prescription = await prisma.prescription.create({
        data: {
          consultationId: consultation.id,
          medications: medications,
          instructions: instructions,
        },
        include: { consultation: true },
      });

      return res.status(201).json(prescription);
    }

    // GET - Lister les ordonnances (optionnel, pour debug ou autre vue)
    if (req.method === "GET") {
      const session = await requireRole(req, res, ["ADMIN", "DOCTOR", "RECEPTIONIST", "PATIENT"]);
      if (!session) return;

      let where: any = {};

      // Si patient, filtrer par ses consultations
      if (session.user.role === "PATIENT") {
        const patient = await prisma.patient.findFirst({ where: { ownerId: session.user.id } });
        if (!patient) return res.status(200).json([]);
        where.consultation = { patientId: patient.id };
      }

      const prescriptions = await prisma.prescription.findMany({
        where,
        orderBy: { createdAt: "desc" },
        include: { consultation: { include: { patient: true, doctor: true } } },
      });

      return res.status(200).json(prescriptions);
    }

    res.setHeader("Allow", ["GET", "POST"]);
    return res.status(405).end();
  } catch (err) {
    console.error(err);
    return res.status(500).json({ error: "Internal server error" });
  }
}